html, body, div, span, h1, h2, h3, h4, h5, h6, p, img,
a, ul, ol, li, sub, sup, pre, code, iframe, form, input, button, textarea,
select, option, legend, table, thead, tbody, tfoot, tr,
th, td, caption, section, main, footer, header, nav, aside,
article, blockquote, details, hgroup, figcaption, figure,
video, audio, picture {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

article, aside, details, figcaption, figure,
footer, header, main, hgroup, menu, nav, section {
  display: block;
}

html, body {
  font-size: 100%;
  size-adjust: 100%;
  height: 100%;
}

a, a:hover, a:visited {
  text-decoration: none;
}

ol, ul {
  list-style: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  margin: 0;
}

img {
  border: 0;
  vertical-align: top;
}

@font-face {
  font-family: "Raleway Regular";
  src: url("../fonts/Raleway-Regular.ttf");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Raleway Medium";
  src: url("../fonts/Raleway-Medium.ttf");
  font-style: normal;
  font-weight: 500;
}
:root {
  --font-regular: "Raleway Regular";
  --font-medium: "Raleway Medium";
  --color-bg-primary: #eff0f3;
  --color-bg-secondary: #fffffe;
  --color-text-primary: #2b2c34;
  --color-text-secondary: #fffffe;
  --color-accent-primary: #6246ea;
  --color-accent-primary-hover: #472fc2;
  --color-accent-secondary: #e45858;
}

body {
  background-color: var(--color-bg-primary);
  font-family: var(--font-regular);
  color: var(--color-textr-primary);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus {
  transition: all 5000s ease-in-out 0s;
}

.app {
  display: grid;
  max-width: 33.75rem;
  margin: 0 auto;
  padding: 15vh 0.625rem 5vh;
  gap: 1.875rem 0;
}

.title {
  font-family: var(--font-medium);
  font-size: 2.5rem;
  font-weight: 500;
  text-align: center;
}

.tasks {
  display: grid;
  gap: 1.25rem;
  padding: 1.25rem;
  background-color: var(--color-bg-secondary);
  border-radius: 0.5rem;
  box-shadow: 0 0 1rem rgba(98, 70, 234, 0.08);
}

.tasks-form {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 0 1.25rem;
}
.tasks-form__input {
  width: 100%;
  padding: 0.5rem;
  border: 0;
  border-radius: 0.25rem;
  background-color: var(--color-bg-secondary);
  box-shadow: 0 0 0.5rem rgba(98, 70, 234, 0.1);
  font-size: 1rem;
}
.tasks-form__input:focus {
  outline: 0.125rem var(--color-accent-primary) solid;
}
.tasks-form__button {
  padding: 0.5rem 1rem;
  border: 0;
  border-radius: 0.25rem;
  background-color: var(--color-accent-primary);
  box-shadow: 0 0 0.5rem rgba(98, 70, 234, 0.1);
  font-family: var(--font-medium);
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--color-text-secondary);
  transition: background-color 0.2s ease 0s;
}
.tasks-form__button:hover {
  background-color: var(--color-accent-primary-hover);
}
.tasks-form__button:focus-visible {
  outline: 0.125rem var(--color-text-primary) solid;
}

.tasks-list {
  border-radius: 0.25rem;
  box-shadow: 0 0 0.5rem rgba(98, 70, 234, 0.1);
}
.tasks-list__item {
  position: relative;
  padding: 0.3125rem 0;
}
.tasks-list__item:not(:last-child) {
  border-bottom: 0.0625rem rgba(98, 70, 234, 0.1) solid;
}
.tasks-list__form {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 0 0.3125rem;
}
.tasks-list__status {
  position: relative;
  display: flex;
  align-items: center;
  padding: 0.625rem 0.9375rem;
  cursor: pointer;
}
.tasks-list__status input {
  position: absolute;
  left: 0;
  top: 0;
  appearance: none;
}
.tasks-list__status input:focus-visible + .tasks-list__checkbox {
  outline: 0.125rem var(--color-accent-primary) solid;
}
.tasks-list__status input:checked + .tasks-list__checkbox {
  stroke: var(--color-accent-primary);
}
.tasks-list__checkbox {
  width: 0.75rem;
  height: 0.75rem;
  padding: 0.125rem;
  border: 0.0625rem rgba(98, 70, 234, 0.2) solid;
  border-radius: 0.25rem;
}
.tasks-list__input {
  width: 100%;
  padding: 0.3125rem;
  border: 0.0625rem rgba(98, 70, 234, 0.2) solid;
  border-radius: 0.25rem;
  font-family: var(--font-medium);
  font-size: 1rem;
  font-weight: 500;
}
.tasks-list__input:focus {
  outline: 0.125rem var(--color-accent-primary) solid;
}
.tasks-list__input:disabled {
  box-shadow: none;
  background: transparent;
  border: 0.0625rem rgba(98, 70, 234, 0) solid;
  color: var(--color-textr-primary);
}
.tasks-list__input[data-completed=true] {
  text-decoration: line-through;
  color: rgba(43, 44, 52, 0.5);
}
.tasks-list__button {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0 0.625rem;
  min-height: 2.5rem;
  padding: 0.625rem 0.9375rem;
  border: 0;
  background: transparent;
}
.tasks-list__button.is-active {
  z-index: 15;
}
.tasks-list__button:hover .tasks-list__icon {
  stroke: var(--color-accent-secondary);
}
.tasks-list__button:hover .tasks-list__tool {
  color: var(--color-accent-secondary);
}
.tasks-list__button:hover .tasks-list__icon_more {
  stroke: var(--color-accent-primary);
}
.tasks-list__icon {
  stroke: var(--color-text-primary);
  transition: stroke 0.2s ease 0s;
}
.tasks-list__icon_more {
  width: 0.25rem;
  height: 1.25rem;
}
.tasks-list__icon_edit {
  width: 1.125rem;
  height: 1.125rem;
}
.tasks-list__icon_save {
  width: 1.125rem;
  height: 0.875rem;
}
.tasks-list__icon_delete {
  width: 1.125rem;
  height: 1.125rem;
}
.tasks-list__tools {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 10;
  padding: 0 2.5rem 0 0;
  background: var(--color-bg-secondary);
  border-radius: 0.25rem;
  box-shadow: 0 0 0.5rem rgba(98, 70, 234, 0.1);
}
.tasks-list__tools.is-hidden {
  display: none;
}
.tasks-list__tool {
  font-family: var(--font-medium);
  font-size: 0.875rem;
  font-weight: 500;
  transition: color 0.2s ease 0s;
}